import React, { Component } from 'react';

import { IconBetmaster } from './IconBetmaster';
import { IconRight } from './IconRight';

import './App.css';

class App extends Component {
  constructor() {
    super();
    this.openChrome = this.openChrome.bind(this);
  }

  openChrome() {
    window.location = 'googlechrome://navigate?url=betmaster.com/';
  }

  render() {
    return (
      <div className="root">
        <div className="bm-logo">
          <IconBetmaster />
          <div className="bm-moto">Betting reinvented</div>
        </div>
        <div className="title">
          Betmaster does not support Opera Mini in this region
        </div>
        <div onClick={this.openChrome} className="button">
          Open Betmaster in Chrome
        </div>
        <div className="text">
          Are you from Africa?<br />
          Visit your regional site:
        </div>
        <a href="http://lite.betmaster.ke">
          <div className="country">
            Kenya
            <span className="right-icon">
              <IconRight />
            </span>
          </div>
        </a>
      </div>
    );
  }
}

export default App;



